import { App, createApp, h, reactive, ref } from 'vue';
import Dq from './index.vue';

/** dom */
let $vm = null;

/** keyboardprops */

const $ref = ref(null)

const props = reactive({
  user: '66647000',
  config: {}
})

/**  */
const globalConfig = {
  
}

/**  controller */
const $dq = {
  /** show */
  changeConfig(val){
    Object.assign(props.config, val)
  },
  $ref
}

export default {
  install: (app, options) => {
    if (!$vm) {
      const wrapper = document.createElement('div');
      wrapper.id = 'dq';
      document.body.appendChild(wrapper)
  
      $vm = createApp({
        setup() {
          return () => {
            return h(Dq, {
              ...props,
              ref: $ref
            })
          }
        }
      }).mount(wrapper);
      /** controller */
      app.config.globalProperties.$dq = $dq
    }
  }
}